<template>
    <div class="yingyong">
        <headerBar :title="headerNavData" @gomycenter="gomycenter()"></headerBar>
        <div class="codelist">
          <div class="code_list" v-for="(item,index) in codelist" @click="requirecode(item)">
            <yd-flexbox class="code_list_item">
                <img src="../assets/images/chose.png" style="width: 20px;height: 20px;" v-if="item.publickey==publickey">
                <div>
                  <canvas  :id="item.src" :style="{width: '.8rem', height: '.8rem'}" style="border-radius:50%;margin: 0 .1rem 0 .2rem"></canvas>
                </div>
                <yd-flexbox-item class="code_list_name">
                  <p>{{item.name}}</p>
                  <p>{{item.publickey1}}</p>
                </yd-flexbox-item>
            </yd-flexbox>
            <yd-flexbox class="code_list_num">
                <div><span class="zwf"></span></div>
                <yd-flexbox-item class="code_list_how">
                  <p><!-- <span>{{item.bdknum}}</span>BDK --></p>
                  <p><span>{{item.num}}</span>Ether</p>
                </yd-flexbox-item>
            </yd-flexbox>
            <div class="code_list_one">{{index+1}}</div>
          </div>
          
          <!-- --- -->
        </div>
        <div class="code_btn">
          <div class="left_btn"  @click="gocreatecode()">
            <yd-flexbox>
                <div><img src="../assets/images/sdf5.png"></div>
                <yd-flexbox-item>{{$t('header.createcode')}}</yd-flexbox-item>
            </yd-flexbox>
          </div>
          <div class="right_btn" @click="goimporcode()">
            <yd-flexbox>
                <div><img src="../assets/images/tyfg2.png"></div>
                <yd-flexbox-item>{{$t('header.importcode')}}</yd-flexbox-item>
            </yd-flexbox>
          </div>
        </div>
    </div>
  
</template>

<script>
  import {getCookie,setCookie,getCookieWithDefault} from '../assets/js/cookie.js'
  import { getTokenQty } from '../assets/js/Keytoken.js'
  import headerBar from './header.vue'
export default {
  beforeCreate () {
    var url = this.$route.path
    if (url == "/codelist") {
      setTimeout(function(){
        document.querySelector('body').setAttribute('style', 'background:#f4f4f4')
      },10)
    }else{

    }
  },
  beforeDestroy () {
    document.querySelector('body').setAttribute('style', 'background:#ffffff')
  },
  components:{
      "headerBar":headerBar
  },
  data () {
    return {
      codelist:[],
      publickey:getCookie('publickey'),
      headerNavData:{
         left_g:false,
         left:true,
         title:this.$t('mycenter.wallet'),
         right:false,
         bj:'#2c94ed',
         search:false,
         share:false
      }
    }
  },
  created(){
    var codelist = getCookie("codelist")
    if(codelist&&codelist!=undefined&&codelist!=null&&codelist!="undefined"&&codelist!="null"){
      var arry = JSON.parse(codelist)
      arry.forEach(item => {
          item.num = "--"
          item.bdknum = "--"
          var pu = item.publickey.substring(0,8)
          var pu1 = item.publickey.substring(34,item.publickey.length)
          item.publickey1 = pu + "..." + pu1
          this.walletAccount(item.publickey,item)
      });
      this.codelist = arry
      var a = setInterval(function(){
        //this.test(this.codelist,0)
        
        clearInterval(a)
      }.bind(this),100)
      if (this.$route.query.del) {
        this.headerNavData.left_g = true
        this.headerNavData.left = false
      }
    }
  },
  updated(){
    var list = this.codelist
    for (var i = 0; i < list.length; i++) {
      this.DrawRandomPic((list[i].publickey).substring(2,42),list[i].src)
    }
  },
  methods:{
     goimporcode(){
      this.$router.push({path:'/importcodes'})
     },
     gocreatecode(){
      this.$router.push({path:'/createcode'})
     },
     //修改钱包信息
      requirecode(item){
          this.$router.push({path: "/requirecode",query:{ publickey:item.publickey,name:item.name}});
      },
      gomycenter(){
        this.$router.push({path:'/mycenter',query:{state:5}})
      },
      walletAccount(publickey,item){
        this.axios({
          url:this.Url + 'ShowCurrency.ashx',
          method:'post',
          headers:{'Content-Type':'application/x-www-form-urlencoded'},
          params: {req:4,publickey:publickey,moneycode: getCookie('coin')}
        }).then(res=>{
          for(var i=0;i<res.data.data.length;i++){
            if(res.data.data[i].currName == "ETH"){
              item.num = res.data.data[i].Balance
            }else if(res.data.data[i].currName == "BDK"){
              item.bdknum = res.data.data[i].Balance
            }
          }
          //console.log(res.data)
        })
      },
      //循环请求coin个数
      test(_list,a){
        var that = this
        if(a<_list.length){
            _list[a].coinprice=0
            _list[a].num=0
            var pu = _list[a].publickey.substring(0,8)
            var pu1 = _list[a].publickey.substring(34,_list[a].publickey.length)
            _list[a].publickey1 = pu + "..." + pu1
            _list[a].src = 'drawing'+a
            getTokenQty(_list[a].publickey,_list[a].contractAddress,"eth",_list[a],function(obj){
                that.test(_list,++a);
            })
        }
    },
  },
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.yingyong{
  padding-top: .9rem;
  padding-bottom: .7rem;
  font-family: -apple-system, BlinkMacSystemFont, "PingFang SC","Helvetica Neue",STHeiti,"Microsoft Yahei",Arial,Tahoma,Simsun,sans-serif;
}
.codelist{
  padding: 0 .4rem 1rem .4rem;
}
.code_list{
  padding: .4rem .4rem .2rem .4rem;
  background: #fff;
  position: relative;
  border-radius: 8px;
  box-shadow: 0 3px 6px #ddd;
  border-top: 1px solid #f7f7f7;
  margin-top: .4rem;
}
.code_list_one{
  position: absolute;
  top: -1px;
  right: .2rem;
  z-index: 1;
  width: .36rem;
  height: .45rem;
  text-align: center;
  background: url('../assets/images/1.png') no-repeat top left;
  background-size: 100%;
  color: #fff;
  font-size: .3rem
}
.code_list_item{
  border-bottom: 1px solid #f4f4f4;
  padding-bottom: .35rem
}
.code_list_item img{
  width: .8rem;
  height: .8rem;
  border-radius: 50%;
  border:1px solid #e7f1fa;
}
.zwf{
  width: .8rem;
  height: .8rem;
  border-radius: 50%;
  display: block;
}
.code_list_name,.code_list_how{
  padding-left: .2rem;
}
.code_list_name p:nth-child(1){
  font-size: .28rem;
  color: #181818;
  padding-bottom: .1rem;
}
.code_list_name p:nth-child(2){
  font-size: .23rem;
  color: #b6b6b6;
}
.code_list_how  p:nth-child(1){
  float: left;
  font-size: .23rem;
  color: #b6b6b6;
}
.code_list_how  p:nth-child(2){
  float: right;
  font-size: .23rem;
  color: #b6b6b6;
}
.code_list_how  p span{
  font-size: .3rem;
  color: #181818;
  display: inline-block;
  margin-right: .1rem;
}
.code_list_num{
  padding-top: .25rem
}
.code_btn img{
  width: .8rem;
  height: .8rem;
  border-radius: 50%;
  margin-right: .1rem;
  margin-left: .7rem
}
.code_btn{
  font-size: .3rem;
  color: #111;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  padding: .2rem;
  border-top: 1px solid #eee;
  background: #fff;
  z-index: 1
}
.left_btn{
  float: left;
  width: 49%;
  border-right: 1px solid #eee;
}
.right_btn{
  float: right;
  width: 49%;
}
</style>



